import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(0);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const [count, setcount] = useState(0);

  const [otherFactors, setOtherFactors] = useState(state.otherFactors22to28);

  const Questions = [
    {
      question:
        "Have you EVER committed, assisted in committing, or attempted to commit, a crime or offense for which you were NOT arrested?",
      name: "q22",
    },
    {
      question:
        "Have you EVER been arrested, cited, or detained by any law enforcement officer (including any immigration official or any official of the U.S. armed forces) for any reason?",
      name: "q23",
    },
    {
      question:
        "Have you EVER been arrested, cited, or detained by any law enforcement officer (including any immigration official or any official of the U.S. armed forces) for any reason?",
      name: "q24",
    },
    {
      question: "Have you EVER been convicted of a crime or offense?",
      name: "q25",
    },
    {
      question:
        "Have you EVER been placed in an alternative sentencing or a rehabilitative program (for example, diversion, deferred prosecution, withheld adjudication, deferred adjudication)?",
      name: "q26",
    },
  ];

  const blankOrganization = {
    why: "",
    Datearrested: "",
    where: "",
    outcome: "",
  }
  const [organization, setOrganization] = useState(state.q29);


  const handleChangeorg = (index, name, value) => {
    const values = organization;
    console.log(values[1]);
    values[index][name] = value;
    setOrganization(values);
    onChangeField("e", "q29", organization);
  };

  const addOrganization = () => {
    const values = [...organization];
    validate();
    values.push(blankOrganization);
    setOrganization(values);
  };

  const removeOrganization = (index) => {
    const values = [...organization];
    values.splice(index, 1);
    console.log(index);
    setOrganization(values);
  };

  const validate = () => {
    setTouched(true);
    var isValid = false;
    if (formRef.current !== null) {
      isValid = formRef.current.checkValidity();
    }
    setErrorMsg(count < 4 ? "Please fill out proper values" : "");
  };

  useEffect(() => {
    setTouched(true)
  }, []);

  const handleChange = (name, value) => {
    const values = otherFactors;
    values[name] = value;
    setOtherFactors(values);
    setcount(count + 1);
    validate();
    onChangeField("e", "otherFactors22to28", otherFactors);
  };

  
  const ChangeField = (ind, name, value) => {
    const values = state.otherFactors22to28_multiple;
    values[name] = value;
    onChangeField("othe", "otherFactors22to28_multiple", values);
  };


  return (
    <Step heading={" Other Factors "}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <h6>
        If any of following apply to you, you must answer "Yes" even if your
        records have been sealed, expunged, or otherwise cleared. You must
        disclose this information even if someone, including a judge, law
        enforcement officer, or attorney, told you that it no longer constitutes
        a record or told you that you do not have to disclose the information.
      </h6>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {Questions.map((q, index) => (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        {index + 1}
                        {". "}
                        {q.question}
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check mr-3">
                        <input
                          type="radio"
                          class="form-check-input"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, true);
                            setTouched(true);
                          }}
                          checked={otherFactors[q.name] === true ? true : false}
                          label="Y"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                          
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Yes
                        </label>
                      </div>
                      <div class="form-check mr-2">
                        <input
                          type="radio"
                          class="form-check-input mr-1"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, false);
                            setTouched(true);
                          }}
                          checked={
                            otherFactors[q.name] === false ? true : false
                          }
                          label="N"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                </MDBCard>
              ))}

              <MDBCol md={12}>
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        Have you EVER received a suspended sentence, been placed
                        on probation, or been paroled?
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check mr-3">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="q27a"
                          onClick={() => {
                            ChangeField("other", "q27a", "Y");
                            setTouched(true);
                          }}
                          checked={
                            state.otherFactors22to28_multiple["q27a"] == "Y"
                              ? true
                              : false
                          }
                          label="Y"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                          
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Yes
                        </label>
                      </div>
                      <div class="form-check mr-2">
                        <input
                          type="radio"
                          class="form-check-input mr-1"
                          name="q27a"
                          onClick={() => {
                            ChangeField("other", "q27a", "N");
                            setTouched(true);
                          }}
                          checked={
                            state.otherFactors22to28_multiple["q27a"] == "N"
                              ? true
                              : false
                          }
                          label="N"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                  {state.otherFactors22to28_multiple["q27a"] == "Y" && (
                    <>
                      <MDBCol md={12}>
                        <div>
                          <h6
                            style={{
                              width: "100%",
                              fontSize: "larger",
                              fontWeight: "bold",
                            }}
                          >
                            have you completed the probation or parole?
                          </h6>
                        </div>
                      </MDBCol>
                      <MDBCol>
                        <MDBFormInline className="justify-content-center">
                          <div class="form-check mr-3">
                            <input
                              type="radio"
                              class="form-check-input"
                              name={`q27b`}
                              onClick={() => {
                                ChangeField("other", "q27b", "Y");
                                setTouched(true);
                              }}
                              checked={
                                state.otherFactors22to28_multiple["q27b"] ===
                                "Y"
                                  ? true
                                  : false
                              }
                              label="Y"
                              type="radio"
                              id="radio1"
                              style={{ display: "flex" }}
                              containerClass="mr-5"
                              
                            />
                            <label
                              class="form-check-label"
                              for="materialUnchecked"
                            >
                              Yes
                            </label>
                          </div>
                          <div class="form-check mr-2">
                            <input
                              type="radio"
                              class="form-check-input mr-1"
                              name={"q27b"}
                              onClick={() => {
                                ChangeField("other", "q27b", "N");
                                setTouched(true);
                              }}
                              checked={
                                state.otherFactors22to28_multiple["q27b"] ===
                                "N"
                                  ? true
                                  : false
                              }
                              label="N"
                              type="radio"
                              id="radio2"
                              style={{ display: "flex" }}
                            />
                            <label
                              class="form-check-label"
                              for="materialUnchecked"
                            >
                              No
                            </label>
                          </div>
                        </MDBFormInline>
                      </MDBCol>
                    </>
                  )}
                </MDBCard>
              </MDBCol>

              <MDBCol md={12}>
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        Have you EVER been in jail or prison?
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check mr-3">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="q28a"
                          onClick={() => {
                            ChangeField("other", "q28a", "Y");
                            setTouched(true);
                          }}
                          checked={
                            state.otherFactors22to28_multiple["q28a"] == "Y"
                              ? true
                              : false
                          }
                          label="Y"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                          
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Yes
                        </label>
                      </div>
                      <div class="form-check mr-2">
                        <input
                          type="radio"
                          class="form-check-input mr-1"
                          name="q28a"
                          onClick={() => {
                            ChangeField("other", "q28a", "N");
                            setTouched(true);
                          }}
                          checked={
                            state.otherFactors22to28_multiple["q28a"] == "N"
                              ? true
                              : false
                          }
                          label="N"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                  {state.otherFactors22to28_multiple["q28a"] == "Y" && (
                    <>
                      <MDBCol md={12}>
                        <div>
                          <h6
                            style={{
                              width: "100%",
                              fontSize: "larger",
                              fontWeight: "bold",
                            }}
                          >
                            how long were you in jail or prison?
                          </h6>
                        </div>
                      </MDBCol>
                      <MDBCol>
                        <MDBFormInline className="justify-content-center">
                          <div class="form-check ">
                            <MDBInput
                              type="tel"
                              label="Years"
                              name="q28byears"
                              color="pink"
                              value={
                                state.otherFactors22to28_multiple["q28byears"]
                              }
                              onChange={(e) =>
                                ChangeField(
                                  "other",
                                  "q28byears",
                                  e.target.value
                                )
                              }
                            />
                          </div>
                          <div class="form-check ">
                            <MDBInput
                              type="tel"
                              label="Months"
                              name="q28bmonths"
                              color="pink"
                              value={
                                state.otherFactors22to28_multiple["q28bmonths"]
                              }
                              onChange={(e) =>
                                ChangeField(
                                  "other",
                                  "q28bmonths",
                                  e.target.value
                                )
                              }
                            />
                          </div>
                          <div class="form-check">
                            <MDBInput
                              type="tel"
                              label="Days"
                              name="q28bdays"
                              color="pink"
                              value={
                                state.otherFactors22to28_multiple["q28bdays"]
                              }
                              onChange={(e) =>
                                ChangeField(
                                  "other",
                                  "q28bdays",
                                  e.target.value
                                )
                              }
                            />
                          </div>
                        </MDBFormInline>
                      </MDBCol>
                    </>
                  )}
                </MDBCard>
              </MDBCol>
              <h6 className="mt-5">
                if you answered "No" to all the above questions skip the below
                field and proceed to next step{" "}
              </h6>
              <h6>
                if you answered "Yes" to any of the above questions, complete the following information
              </h6>
              <MDBRow center className="mt-5" md={10}>
                {organization.map((o, index) => (
                  <MDBCard className="mt-1" md={10}>
                    <div className="p-4">
                      <MDBRow>
                        <MDBCol>
                          <MDBInput
                            label="Why were you arrested charged ?"
                            group
                            type="text"
                            name="why"
                            value={o.why}
                            onChange={(e) => {
                              validate();
                              handleChangeorg(
                                index,
                                e.target.name,
                                e.target.value
                              );
                            }}
                            
                            containerClass="mb-0"
                          />
                        </MDBCol>
                      </MDBRow>
                      <h6>
                        (no charges filed, charges dismissed, jail, probation,
                        etc.)
                      </h6>
                      <MDBRow className="pt-3">
                        <MDBCol>
                          <MDBInput
                            label="Date arrested, charged"
                            group
                            type="date"
                            name="Datearrested"
                            value={o.Datearrested}
                            onChange={(e) => {
                              validate();
                              handleChangeorg(
                                index,
                                e.target.name,
                                e.target.value
                              );
                            }}
                            
                            containerClass="mb-0"
                          />
                        </MDBCol>
                        <MDBCol md={12}>
                          <MDBInput
                            label="Place you were arrested"
                            group
                            type="text"
                            name="where"
                            value={o.where}
                            onChange={(e) => {
                              validate();
                              handleChangeorg(
                                index,
                                e.target.name,
                                e.target.value
                              );
                            }}
                            
                            containerClass="mb-0"
                          />
                        </MDBCol>
                      </MDBRow>
                      <h6>(city or town, state, country)</h6>
                      <MDBRow>
                        <MDBCol>
                          <MDBInput
                            label="Outcome"
                            group
                            type="text"
                            name="outcome"
                            value={o.outcome}
                            onChange={(e) => {
                              validate();
                              handleChangeorg(
                                index,
                                e.target.name,
                                e.target.value
                              );
                            }}
                            
                            containerClass="mb-0"
                          />
                          <h6>
                            (no charges filed, charges dismissed, jail,
                            probation, etc.)
                          </h6>
                        </MDBCol>
                      </MDBRow>
                    </div>
                  </MDBCard>
                ))}
              </MDBRow>
              <MDBRow center className="d-flex  align-items-center mt-3 mb-3">
                <MDBCol className="d-flex justify-content-end">
                  <MDBBtn onClick={addOrganization}>+</MDBBtn>
                </MDBCol>
                <MDBCol className="d-flex justify-content-start">
                  <MDBBtn onClick={removeOrganization}>-</MDBBtn>
                </MDBCol>
              </MDBRow>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                      onClick={
                      ()=>{
                         if(state.q29.length< 5) {
                    
                        while(state.q29.length <5)
                        state.q29.push(blankOrganization)
                      }}}
                        
                      
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
